.home_container {
  width: 100%;
  height: 100vh;
  min-height: 100vh;
  background: #000;
  position: relative;
  .center {
    width: 1160px;
    margin: auto;
  }
  .part_title {
    font-size: 60px;
    color: #fff;
    line-height: 90px;
  }
  .h5_btn{
    display: none;
  }
  .icon_menu{
    display: none;
  }
  .socical{
    display: flex;
    width: 48%;
    justify-content: space-between;
    .item{
      display: flex;
      flex-direction: column;
      .tit{
        color: #FFFFFF;
        font-size: 14px;
      }
      .link-item{
        display: flex;
        // align-items: center;
        margin-top: 20px;
        &:hover{
          a{
            color: #fff;
            // transform: scale(1.1);
          }
          .default{
            display: none;
          }
          .hover{
            display: block;
          }
        }
        a{
          color: rgba(#fff,0.7);
          font-size: 14px;
          // transition: all 0.3s;
        }
        img{
          width: 20px;
          height: 20px;
          margin-right: 8px;
          // transition: all 0.3s;
        }
        .hover{
          display: none;
        }
        .blog{
          color: #FFFFFF;
          font-size: 14px;
        }
      }
      .blog-item{
        margin-top: 0;
      }
    }
  }
  .banner{
    position: relative;
    height: 1080px;
    margin-bottom: -100px;
    .fullscreen{  
      width: 100%;
      position: absolute;
      top: -100px;
      height: 100%;
    }
    .content{
      width: 100%;
      position: absolute;
      left: 0;
      top: 0;
      .header{
        width: 100%;
        margin-top: 48px;
        .center{
          display: flex;
          justify-content: space-between;
        }
        .pc_logo {
          width: 220px;
          height: 42px;
          margin-bottom: 100px;
        }
        .socical{
          width: 40%;
          .item{
            position: relative;
            cursor: pointer;
            &:hover{
              .hover_list{
                display: block;
              }
            }
            .tit{
              margin-top: 12px;
            }
            .hover_list{
              position: absolute;
              top: 36px;
              left: 50%;
              transform: translateX(-50%);
              min-width: 104px;
              background: rgba(84,84,84,0.1);
              border-radius: 8px;
              backdrop-filter: blur(10px);
              padding: 0 20px 20px;
              display: flex;
              flex-direction: column;
              align-items: center;
              display: none;
              a{
                white-space: nowrap;
              }
            }
          }
        }
      }
      .banner_text{
        margin-top: 140px;
        .center{
          height: 380px;
          background: rgba(0,0,0,0.34);
          border-radius: 20px;
          backdrop-filter: blur(20px);
          padding: 64px 0;
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: space-between;
          p{
            font-family: 'Poppins-Bold' !important;
            font-size: 80px;
            text-align: center;
            background: linear-gradient(270deg, #4196FF 0%, #FFFFFF 100%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            font-weight: bold;
            line-height: 80px;
          }
          span{
            font-size: 32px;
            color: #FFFFFF;
            line-height: 32px;
            text-shadow: 2px 2px 17px rgba(0,0,0,0.5);
          }
        }
      }
    }
  }
  .tucana_chain{
    background: url('../new-image/bg@2x.png') 50% no-repeat;
    background-size: cover;
    // height: 1220px;
    padding-top: 120px;
    padding-bottom: 100px;
    .center{
      .part_title{
        text-align: center;
      }
      .h5_tit{
        display: none;
      }
      .label_icon{
        width: 100%;
        display: flex;
        justify-content: space-around;
        .item{
          display:flex;
          flex-direction: column;
          align-items: center;
          width: 25%;
          margin-top: 30px;
          img{
            width: 100px;
            height: 100px;
          }
          .label{
            font-size: 22px;
            color: #FFFFFF;
          }
        }
      }
    }
    .animation_box{
      height: calc(100% - 350px);
      width: 100%;
      z-index: 0;
      overflow: hidden;
      position: relative;
      width: 1250px;
      height: 500px;
      margin: auto;
      margin-top: 100px;
      .img_chain{
        height: 600px;
        width: auto;
        position: absolute;
        top: 42%;
        left: 50%;
        transform: translate(-50%,-50%);
      }
      .img_coin{
        width: 177px;
        height: 176px;
        position: absolute;
        top: 8%;
        left: 27%;
      }
      .img_coin2{
        width: 100px;
        height: 100px;
        position: absolute;
        top: 35%;
        left: 61%;
      }
      .halo_1,.halo_2,.halo_3,.halo_4{
        width: 293px;
        height: 52px;
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        z-index: 0;
      }
      .halo_1{
        bottom: 80%;
      }
      .halo_2{
        bottom: 70%;
      }
      .halo_3{
        bottom: 60%;
      }
      .halo_4{
        bottom: 50%;
      }
      .image_animation {
        opacity: 0;
        transition: opacity 1s;
      }
      
      .halo_1.animate {
        opacity: 1;
        animation: fadeIn 1s 1;
      }
      
      .halo_2.animate {
        opacity: 1;
        animation: fadeIn 1s  1;
      }
      
      .halo_3.animate {
        opacity: 1;
        animation: fadeIn 1s 1;
      }
      
      .halo_4.animate {
        opacity: 1;
        animation: fadeIn 1s 1;
      }
      
      .img_coin.animate {
        opacity: 1;
        animation: moveUpDown 5s infinite;
      }
      
      .img_coin2.animate {
        opacity: 1;
        animation: moveUpDown 5s infinite;
      }
    }
    .desc{
      font-size: 22px;
      color: #FFFFFF;
      line-height: 48px;
    }
  }
  .tucana_provides{
    background: url('../new-image/bg2@2x.png') 50% no-repeat;
    background-size: cover;
    margin-top: 120px;
    padding-bottom: 100px;
    .center{
      .rowList{
        width: 100%;
        display: flex;
        justify-content: space-between;
        margin-top: calc(4% / 2);
        .item{
          // width: 360px;
          width: 32%;
          height: auto;
          background: url('../new-image/bg_amms@2x.png') 50% no-repeat;
          background-size: cover;
          padding-bottom: 40px;
          border-radius: 16px;
          display: flex;
          flex-direction: column;
          align-items: center;
          backdrop-filter: blur(10px);
          img{
            width: 320px;
            height: 320px;
          }
          .tit{
            font-size: 22px;
            color: #FFFFFF;
            line-height: 48px;
            padding: 0 40px;
          }
          .desc{
            font-size: 20px;
            color: rgba(#fff,0.7);
            line-height: 30px;
            margin-top: 20px;
            padding: 0 40px;
          }
        }
        .bg_orderbook{
          background: url('../new-image/bg_orderbook@2x.png') 50% no-repeat;
          background-size: cover;
        }
        .bg_staking{
          background: url('../new-image/bg_staking@2x.png') 50% no-repeat;
          background-size: cover;
        }
        .bg_perpetuals{
          background: url('../new-image/bg_perpetuals@2x.png') 50% no-repeat;
          background-size: cover;
        }
        .bg_launchpad{
          background: url('../new-image/bg_launchpad@2x.png') 50% no-repeat;
          background-size: cover;
        }
      }
      .rowBottomList{
        .item{
          width: 48%;
        }
      }
    }
  }
  .tucana_potv_tit{
    margin-top: 80px;
  }
  .tucana_potv{
    // background: url('../new-image/bg_potv@2x.png') 50% no-repeat;
    // background-size: cover;
    padding-bottom: 200px;
    // height: 940px;
    position: relative;
    .center{
      position: relative;
      height: 200px;
      .tucana_potv_tit_center{
        position: absolute;
        top: 0;
        left: 0;
        z-index: 99;
      }
    }
    .potv_fullscreen{
      width: 100%;
      height: 1000px;
      position: absolute;
      top: 0;
      left: 0;
    }
    .center{
      .desc{
        font-size: 22px;
        color: #FFFFFF;
        line-height: 48px;
      }
    }
    .animation_box{
      // height: calc(100% - 400px);
      position: relative;
      width: 343px;
      margin: auto;
      margin-top: 60px;
      height: 500px;
      .img_ball1{
        width: 219px;
        height: 214px;
        position: absolute;
        top: 18%;
        left: -58%;
        animation: fadeInMove 5s infinite;
        z-index: 5;
      }
      .img_ball2{
        width: 187;
        height: 223px;
        position: absolute;
        top: 13%;
        right: -52%;
        animation: fadeInMove1 5s infinite;
      }
      .img_ball3{
        width: 440px;
        height: 440px;
        position: absolute;
        top: -5%;
        left: 50%;
        transform: translateX(-50%);
      }
    }
  }
  .tucana_potv_list{
    margin-top: -250px;
    display: flex;
    justify-content: space-between;
    .item{
      // width: 360px;
      width: 32%;
      height: 421px;
      background: url('../new-image/img_potv1@2x.png') 50% no-repeat;
      background-size: cover;
      display: flex;
      flex-direction: column;
      justify-content: center;
      border-radius: 16px;
      padding: 0 20px;
      backdrop-filter: blur(10px);
      p{
        font-weight: 600;
        font-size: 22px;
        color: #FFFFFF;
        line-height: 48px;
        margin-top: 50px;
      }
      span{
        color: rgba(#fff,0.7);
        font-size: 20px;
        line-height: 30px;
        margin-top: 16px;
      } 
    }
    .img_potv1{
      background: url('../new-image/img_potv1@2x.png') 50% no-repeat;
      background-size: cover;
    }
    .img_potv2{
      background: url('../new-image/img_potv2@2x.png') 50% no-repeat;
      background-size: cover;
    }
    .img_potv3{
      background: url('../new-image/img_potv3@2x.png') 50% no-repeat;
      background-size: cover;
    }
  
  }
  .tucana_build{
    position: relative;
    padding-bottom: 100px;
    margin-top: 120px;
    .center{
      .desc{
        font-size: 22px;
        color: #FFFFFF;
        line-height: 48px;
      }
      .btn{
        color: #fff;
        margin-top: 30px;
        span::before {
            content: "Get Started";
        }
        span:hover::before {
            content: "Coming Soon";
        }
      }
      .list_animation{
        display: flex;
        .list{
          width: 50%;
          height: 350px;
          display: flex;
          flex-wrap: wrap;
          margin-top: 80px;
          .item{
            width: 50%;
            display: flex;
            flex-direction: column;
            align-items: center;
            p{
              color: #fff;
              font-size: 22px;
            }
            img{
              width: 100px;
              height: 100px;
            }
          }
        }
      }
      .animation{
        position: relative;
        width: 200px;
        height: 660px;
        .img_tucana{
          width: auto;
          height: 100%;
          position: absolute;
          top: 0;
          right: -460px;
          z-index: 1;
        }
        .ball1{
          width: 56px;
          height: 56px;
          position: absolute;
          top: 280px;
          right: -270px;
          z-index: 2;
          display: none; 
          transition: transform 0.5s ease-in-out;
        }
        .ball2{
          width: 79px;
          height: 79px;
          position: absolute;
          top: 190px;
          right: -180px;
          z-index: 2;
          display: none; 
          transition: transform 0.5s ease-in-out;
        }
        .ball3{
          width: 228px;
          height: 228px;
          position: absolute;
          top: 40px;
          right: -100px;
          z-index: 2;
          display: none; 
          transition: transform 0.5s ease-in-out;
        }
        .light1{
          width: 190px;
          height: 241px;
          position: absolute;
          top: 145px;
          right: -330px;
          z-index: 0;
          display: none; 
          transition: transform 0.5s ease-in-out;
        }
        .light2{
          width: 308px;
          height: 241px;
          position: absolute;
          top: 155px;
          right: -390px;
          z-index: 0;
          display: none; 
          transition: transform 0.5s ease-in-out;
        }
        .show-and-float {
          display: block; 
          animation: floatAnimation 4s infinite alternate;
        }
        .show{
          display: block;
          animation: fadeIn 1s 1;
        }
      }
    }
    .animation_box{
      width: 100%;
      height: 800px;
      position: absolute;
      top: 0;
      overflow: hidden;
      padding-right: 100px;
      z-index: -1;
      img{
        position: absolute;
      }
      .bg3{
        position: absolute;
        bottom: -100px;
        left: 50%;
        transform: translateX(-50%);
        height: 320px;
      }
    }
  }
  .tucana_bg4{
    background: url('../new-image/bg4@2x.png') 50% no-repeat;
    background-size: cover;
    height: 100px;
    margin-top: -40px;
  }
  .footer_block{
    width: 100%;
    background: #131313;
    .center{
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
    .pc_logo {
      width: 220px;
      height: 42px;
      margin-bottom: 100px;
    }
    .socical{
      width: 100%;
      margin-bottom: 60px;
      .item{
        display: flex;
        flex-direction: column;
        .tit{
          color: #FFFFFF;
          font-size: 14px;
        }
        .link-item{
          margin-top: 28px;
          position: relative;
          &:hover{
            .contact{
              display: flex;
            }
          }
          .contact{
            position: absolute;
            bottom: 20px;
            right:0%;
            background: #ffffff0d;
            width: 100%;
            width: 150px;
            text-align: center;
            height: 28px;
            border-radius: 8px;
            display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            display: none;
            p{
              font-size: 12px;
              color: #fff;
            }
            img{
              width: 16px;
              height: 16px;
              margin-right: 0;
              margin-left: 4px;
            }
            .down_img{
              margin-left: 0;
              margin-right: 4px;
            }
          }
        }
      }
    }
    .copyright {
      width: 100%;
      border-top: 1px solid rgba(255, 255, 255, 0.1);
      padding-top: 20px;
      font-size: 12px;
      color: #ffffff;
      opacity: 0.5;
      line-height: 12px;
      text-align: center;
      font-weight: 400;
      padding-bottom: 32px;
    }
  }
}

@media screen and (min-width: 1920px) {
  .home_container {
    .center {
      width: 1400px;
    }
  }
}

@media screen and (max-width: 1400px) {
  .home_container {
    .part_title{
      font-size: 48px;
    }
    .banner{
      .content{
        .banner_text{
          .center{
            p{
              font-size: 60px;
            }
          }
        }
      }
    }
    .tucana_chain{
      .animation_box{
        width: 100% !important;
      }
    }
    .tucana_build{
      .center{
        .list_animation{
          .animation{
            margin-left: -60px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .home_container {
    .center {
      width: 100%;
      padding: 0 20px;
    }
    .part_title{
      font-size: 48px;
    }
    .banner{
      .content{
        .banner_text{
          padding: 0 40px;
          .center{
            width: 100%;
            p{
              font-size: 60px;
            }
          }
        }
      }
    }
    .tucana_chain{
      .center{
        .label_icon{
          flex-wrap: wrap;
          .item{
            width: 50%;
          }
          .label{
            white-space: nowrap;
          }
        }
      }
      .animation_box{
        overflow: hidden;
      }
    }
    .tucana_provides{
      .center{
        .content{
          .rowList{
            .item{
              width: 32%;
              padding-bottom: 20px;
              img{
                width: 100%;
                height: auto;
              }
              .tit{
                padding: 0 20px;
              }
              .desc{
                padding: 0 20px;
              }
            }
          }
          .rowBottomList{
            .item{
              width: 49%;
            }
          }
        }
      }
    }
    .tucana_potv{
      .center{
        .tucana_potv_tit_center{
          left: 20px;
        }
      }
    }
    .tucana_potv_list{
      .item{
        width: 32%;
        p{
          line-height: 30px;
          height: 60px;
        }
        span{
          height: 60px;
        }
      }
    }
    .tucana_build{
      .center{
        .list_animation{
          .animation{
            transform: scale(0.8);
          }
        }
      }
      .animation_box{
        overflow: hidden;
      }
    }
  }
}

@media screen and (max-width: 960px) {
  .home_container{
    .part_title{
      font-size: 36px;
    }
    .banner{
      .content{
        .banner_text{
          .center{
            span{
              font-size: 24px;
            }
          }
        }
      }
    }
    .tucana_provides{
      .center{
        .content{
          .rowList{
            .item{
              .desc{
                font-size: 16px;
                line-height: 24px;
              }
            }
          }
        }
      }
    }
    .tucana_potv{
      .animation_box{
        margin-top: 100px;
      }
    }
    .tucana_potv_list{
      .item{
        p{
          font-size: 20px;
        }
        span{
          font-size: 16px;
          line-height: 24px;
        }
      }
    }
    .tucana_build{
      .center{
        .list_animation{
          .animation{
            margin-top: -80px;
            transform: scale(0.68);
          }
        }
      }
    }
  }
}
