              span{
                margin-top: 0;
              }
            }
          }
        }
      }
    }
    .tucana_chain{
      padding-bottom: 60px;
      padding-top: 60px;
      .center{
        .part_title {
          text-align: left;
        }
        .h5_tit{
          display: block;
        }
        .pc_tit{
          display: none;
        }
        .desc{
          font-size: 20px;
          line-height: 40px;
          margin-top: -100px;
        }
        .label_icon{
          .item{
            width: 50%;
            margin-bottom: 40px;
            .label{
              min-width: 150px;
              text-align: center;
              font-size: 16px;
              white-space: wrap;
            }
          }
        }
      }
      .animation_box{
        margin-top: 0;
        .img_chain{
          height: 400px;
        }
        .halo_1 ,.halo_2 ,.halo_3 ,.halo_4{
          width: 183px;
          height: 33px;
        }
        .img_coin{
          width: 110px;
          height: 110px;
          left: 2%;
        }
        .img_coin2{
          width:63px;
          height: 63px;
          left: 75%;
        }
      }
    }
    .tucana_provides{
      margin: 60px 0;
      margin-bottom: 0;
      .center{
        .content{
          .rowList{
            flex-wrap: wrap;
            .item{
              width: 100%;
              margin-top: 20px;
              img{
                width: 343px;
              }
              &:nth-child(1){
                margin-top: 0;
              }
            }
          }
        }
      }
    }
    .tucana_potv{
